<template>
  <transition name="slide">
    <div class="add-song" v-show="showFlag" @click.stop>
      <div class="header">
        <h1 class="title">添加歌曲到列表</h1>
        <div class="close" @click="hide">
          <i class="icon-close"></i>
        </div>
      </div>
      <div class="search-box-wrapper">
        <search-box placeholder="搜索歌曲" ref="searchBox" @query="handleQuery"></search-box>
      </div>
      <div class="shortcut" v-show="!query">
        <switches :switches="switches"
                  :currentIndex="currentIndex"
                  @switchesItem="switchesItem"
                  >
        </switches>
        <div class="list-wrapper">
          <scroll class="list-scroll" ref="songList" v-if="currentIndex === 0" :data="playHistory">
            <div class="list-inner">
              <song-list :songs="playHistory" @select="selectSong"></song-list>
            </div>
          </scroll>
          <scroll class="list-scroll"
                  ref="searchList" v-if="currentIndex === 1"
                  :data="searchHistory" :refreshDelay="refreshDelay">
            <div class="list-inner">
              <search-list :searches="searchHistory"
                           @select="addQuery"
                           @delete="deleteSearchHistory"
              ></search-list>
            </div>
          </scroll>
        </div>
      </div>
      <div class="search-result" v-show="query">
        <suggest :query="query" :showSinger="false"
                 @select="selectSuggest"
                 @listenScroll="listenScroll"
        ></suggest>
      </div>
      <top-tip ref="topTip">
        <div class="tip-title">
          <i class="icon-ok"></i>
          <span class="text">1首歌已添加到播放队列</span>
        </div>
      </top-tip>
    </div>
  </transition>
</template>
<script>
import SearchBox from 'base/search-box/searchBox'
import switches from 'base/switches/switches'
import SearchList from 'base/search-list/searchList'
import SongList from 'base/song-list/song-list'
import Suggest from 'components/suggest/suggest'
import scroll from 'base/scroll/scroll'
import TopTip from 'base/top-tip/toptip'
import Song from 'common/js/song'
import {searchMixin} from 'common/js/mixin'
import {mapGetters, mapActions} from 'vuex'
export default {
  mixins: [searchMixin],
  name: 'AddSong',
  components: {
    SearchBox,
    switches,
    SearchList,
    SongList,
    Suggest,
    scroll,
    TopTip
  },
  data () {
    return {
      showFlag: false,
      switches: [
        {
          name: '最近播放'
        },
        {
          name: '搜索历史'
        }
      ],
      currentIndex: 0,
      refreshDelay: 120
    }
  },
  computed: {
    ...mapGetters([
      'playHistory'
    ])
  },
  methods: {
    show () {
      this.showFlag = true
      this.refreshList()
    },
    hide () {
      this.showFlag = false
    },
    selectSuggest () {
      this.$refs.topTip.show()
      this.saveSearch()
    },
    refreshList () {
      setTimeout(() => {
        if (this.currentIndex === 0) {
          this.$refs.songList.refresh()
        } else if (this.currentIndex === 1) {
          this.$refs.searchList.refresh()
        }
        // this.$refs.suggest.refresh()
      }, 20)
    },
    switchesItem (index) {
      this.currentIndex = index
      // console.log(this.currentIndex)
    },
    selectSong (song, index) {
      if (index !== 0) {
        this.insertSong(new Song(song))
      }
      this.$refs.topTip.show()
    },
    ...mapActions([
      'insertSong'
    ])
  },
  watch: {
    query (newQ) {
      if (!newQ) {
        this.refreshList()
      }
    }
  }
}
</script>
<style lang="stylus" scoped>
  @import '~common/stylus/variable'
  @import '~common/stylus/mixin'
  .add-song
    position: fixed
    top 0
    bottom 0
    width 100%
    z-index 200
    background $color-background
    &.slide-enter-active, &.slide-leave-active
      transition all 0.3s
    &.slide-enter, &slide-leave-to
      transform translate3d(100%, 0, 0)
    .header
      position relative
      height 44px
      text-align center
      .title
        line-height 44px
        font-size $font-size-large
        color $color-text
      .close
        position absolute
        top 0
        right 8px
        .icon-close
          display block
          padding 12px
          font-size 20px
          color $color-theme
    .search-box-wrapper
      margin 20px
    .shortcut
      .list-wrapper
        position absolute
        top 165px
        width 100%
        bottom 0
        .list-scroll
          height 100%
          overflow hidden
          .list-inner
            padding 20px 30px
    .search-result
      position fixed
      width 100%
      top 124px
      bottom 0
    .tip-title
      text-align center
      padding 18px 0
      font-size 0
      .icon-ok
        font-size $font-size-medium
        color $color-theme
        margin-right 4px
      .text
        font-size $font-size-medium
        color $color-text
</style>



